@OptIn(ExperimentalResourceApi::class, ExperimentalComposeUiApi::class)
@Composable
fun ExchangeBenefits(
    component: LandingComponent,
    modifier: Modifier
) {
    val screenWidth = screenWidth
    val spacing = MaterialTheme.dimens.spacing
    Box(
        modifier = modifier
            .background(MaterialTheme.colorScheme.surface)
            .padding(bottom = remember(screenWidth) { screenWidth * 0.04f + spacing.extraLarge })
    ) {
        Image(
            modifier = Modifier
                .width(remember(screenWidth) { screenWidth * 0.161f })
                .offset(y = remember(screenWidth) { screenWidth * 0.02f }),
            painter = painterResource(Res.drawable.stamp_1),
            contentDescription = null
        )
        Box(
            modifier = Modifier.fillMaxSize(),
            contentAlignment = Alignment.TopEnd
        ) {
            Image(
                modifier = Modifier.width(remember(screenWidth) { screenWidth * 0.104f })
                    .offset(y = remember(screenWidth) { screenWidth * 0.07f }),
                painter = painterResource(Res.drawable.stamp_3),
                contentDescription = null
            )
        }
        Column(
            modifier = Modifier
                .fillMaxWidth()
                .padding(top = remember(screenWidth) { screenWidth * 0.124f }),
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Text(
                text = stringResource(Res.string.label_exchange_benefits_1),
                style = MaterialTheme.typography.bodyLarge
            )
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.large))
            Text(
                modifier = Modifier.fillMaxWidth(0.6f),
                text = stringResource(Res.string.label_exchange_benefits_2),
                style = MaterialTheme.typography.headlineMedium,
                textAlign = TextAlign.Center
            )
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.large))

            Text(
                text = stringResource(Res.string.label_exchange_benefits_3),
                style = MaterialTheme.typography.bodyLarge,
                fontWeight = FontWeight.Bold,
            )
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge))

            Row(
                modifier = Modifier.fillMaxWidth()
            ) {
                Box(
                    modifier = Modifier.weight(1f, true)
                ) {
                    Image(
                        modifier = Modifier
                            .width(remember(screenWidth) { screenWidth * 0.063f })
                            .offset(y = MaterialTheme.dimens.spacing.large),
                        painter = painterResource(Res.drawable.stamp_4),
                        contentDescription = null
                    )
                }
                PrimaryButton(onPurchaseClick = component.onMentoryClick)

                Box(
                    modifier = Modifier.weight(1f, true),
                    contentAlignment = Alignment.Center
                ) {
                    Image(
                        modifier = Modifier
                            .width(remember(screenWidth) { screenWidth * 0.08f })
                            .offset(y = -MaterialTheme.dimens.spacing.extraLarge),
                        painter = painterResource(Res.drawable.stamp_2),
                        contentDescription = null
                    )
                }
            }
        }
    }
}
