@OptIn(ExperimentalResourceApi::class)
@Composable
fun ToolbarAdaptive(
    showMenuScreen:()->Unit
) {
    val screenWidth = screenWidth

    Box(
        modifier = Modifier.fillMaxSize(),
    ) {
        Row(
            modifier = Modifier.fillMaxWidth()
                .padding(
                    horizontal = MaterialTheme.dimens.spacing.medium,
                    vertical = MaterialTheme.dimens.spacing.extraLarge
                ),
        ) {
            Image(
                modifier = Modifier.width(LocalDimens.current.iconSize.logoWidth),
                painter = painterResource(Res.drawable.logo),
                contentDescription = stringResource(Res.string.cd_logo)
            )
            Spacer(Modifier.weight(1.0f, true))

            Image(
                modifier = Modifier
                    .size(MaterialTheme.dimens.iconSize.toolbarIconSize)
                    .clickable { showMenuScreen() }
                    .padding(MaterialTheme.dimens.spacing.small),
                painter = painterResource(Res.drawable.menu),
                contentDescription = stringResource(Res.string.cd_menu)
            )
        }
    }
}


@OptIn(ExperimentalResourceApi::class)
@Composable
fun ToolbarAdaptiveScreen(
    textActionButtons: List<TextActionButton>,
    imageActionButtons: List<ImageActionButton>,
    onCloseClick: () -> Unit
) {
    val screenWidth = screenWidth
    Box(
        modifier = Modifier
            .fillMaxSize()
            .background(MaterialTheme.colorScheme.background)
    ) {
        Box(
            modifier = Modifier
                .fillMaxWidth()
                .padding(MaterialTheme.dimens.spacing.medium),
            contentAlignment = Alignment.TopEnd
        ) {
            Image(
                modifier = Modifier
                    .size(MaterialTheme.dimens.iconSize.toolbarIconSize)
                    .clickable { onCloseClick() }
                    .padding(MaterialTheme.dimens.spacing.small),
                painter = painterResource(Res.drawable.close),
                contentDescription = stringResource(Res.string.cd_menu)
            )
        }
        Circle(
            modifier = Modifier
                .offset(
                    x = remember(screenWidth) { -screenWidth * 0.058f },
                    y = remember(screenWidth) { screenWidth * 0.159f }
                ),
            radius = remember(screenWidth) { screenWidth * 0.084f },
            color = MaterialTheme.colorScheme.secondary
        )
        DashedCircle(
            modifier = Modifier
                .offset(
                    x = remember(screenWidth) { -screenWidth * 0.058f },
                    y = remember(screenWidth) { screenWidth * 0.128f }
                ),
            radius = remember(screenWidth) { screenWidth * 0.084f }
        )

        Box(
            modifier = Modifier.fillMaxSize(),
            contentAlignment = Alignment.BottomEnd
        ) {
            Circle(
                modifier = Modifier
                    .offset(
                        x = remember(screenWidth) { screenWidth * 0.207f },
                        y = remember(screenWidth) { screenWidth * 0.087f }
                    ),
                radius = remember(screenWidth) { screenWidth * 0.202f },
                color = MaterialTheme.colorScheme.primary
            )
            DashedCircle(
                modifier = Modifier
                    .offset(
                        x = remember(screenWidth) { screenWidth * 0.153f },
                        y = remember(screenWidth) { screenWidth * 0.107f }
                    ),
                radius = remember(screenWidth) { screenWidth * 0.202f }
            )
        }
        Column(
            modifier = Modifier.fillMaxSize(),
            verticalArrangement = Arrangement.Center,
            horizontalAlignment = Alignment.CenterHorizontally
        ) {
            Spacer(Modifier.weight(1f,true))
            Image(
                modifier = Modifier.fillMaxWidth(0.33f),
                painter = painterResource(Res.drawable.logo),
                contentDescription = stringResource(Res.string.cd_logo)
            )
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge))
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge))
            TextActionButtons(textActionButtons)
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge))
            Spacer(modifier = Modifier.height(MaterialTheme.dimens.spacing.extraLarge))
            Row {
                ImageActionButtons(imageActionButtons)
            }
            Spacer(Modifier.weight(1f,true))
        }
    }
}